import React from 'react';
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import Input from "../Input";
import styles from '../style.module.css';

export default () => (
  <Row className={"mt-4"}>

    <Col className={"col-6 m-0 pr-2 text-center"}>
      <Col className={styles.subheader}>
        Introduce your personal data
      </Col>

      <Input placeholder={'Name'} className={"mt-4"}/>
      <Input placeholder={'Surname'}/>
      <Input placeholder={'Date of birth'}/>
    </Col>

    <Col className={"col-6 m-0 mb-3 pl-2 text-center"}>
      <Col className={styles.subheader}>
        Address
      </Col>

      <Row className={"mt-4"}>
        <Col className={"col-8 pr-2"}>
          <Input placeholder={'Street'}/>
          <Input placeholder={'City'}/>
          <Input placeholder={'Country'}/>
        </Col>
        <Col className={"col-4 pl-2"}>
          <Input placeholder={'№'}/>
          <Input placeholder={'Index'}/>
          <Input placeholder={'State'}/>
        </Col>
      </Row>

      <Input placeholder={'Work email'}/>
      <Input placeholder={'Alternative email'}/>
      <Input placeholder={'Gender'}/>
      <Input placeholder={'Facebook profile'}/>
      <Input placeholder={'Twitter profile'}/>
      <Input placeholder={'Linked-in profile'}/>
    </Col>

  </Row>
);

