import React from 'react';
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import styles from '../style.module.css';
import {TextArea} from "informed";

const Area = (props) => (
  <Col>
    <Col className={styles.areaHeader}>{props.title}</Col>
    <TextArea className={styles.areaBusiness} {...props}/>
  </Col>
);

export default () => (
  <Row className={"mt-4"}>
    <Row className={styles.subheader + ' mb-3'}>
      Business
    </Row>

    <Row>
      <Col className={"col-6 m-0 pr-2 text-center"}>
        <Area title={"Business model"} placeholder={'description of business model ex: production business, retail business service supply business'}/>
        <Area title={"Year average or final production capacity"} placeholder={'trasportation methods and conditions'}/>
      </Col>
      <Col className={"col-6 m-0 pl-2 text-center"}>
        <Area title={"Experience"} placeholder={'years, locations, domains of experience'}/>
        <Area title={"Delivery model"} placeholder={'geographical locations and possibility to deliver the goods'}/>
      </Col>
    </Row>

    <Row>
      <Area title={"Business mission and vision"} placeholder={'2 propositions'}/>
    </Row>

  </Row>
)