import React, {useState} from 'react';
import styles from './style.module.css';
import Col from "react-bootstrap/Col";
import cn from 'classnames';
import Row from "react-bootstrap/Row";
import Container from "react-bootstrap/Container";
import Profile from "./contents/Profile";
import Wrapper from "../common/core/Wrapper";
import Button from "../common/core/Button";
import Image from "react-bootstrap/Image";

import logoImagePath from "./images/logo_sm.png";
import SignFooter from "../common/parts/SignFooter";
import Company from "./contents/Company";
import Business from "./contents/Business";
import withOnLoadRender from "../../hocs/withOnLoadRender";

const WIDTH = 700;
const BAR_UNIT_WIDTH = 70;
const UNIT_ROUND = 30;

const STEP_TEXTS = ['Profile', 'Company', 'Business'];
const STEP_CONTENTS = [Profile, Company, Business, () => <div>Congratulations!...</div>];
const LAST_STEP = STEP_CONTENTS.length-1;

const SignUp = () => {
  const [step, setStep] = useState(0);

  const nextStep = () => {
    if (step === LAST_STEP) return;
    setStep(step+1);
    window.scrollTo({top: 0});
  };
  
  const CurrentStepComponent = STEP_CONTENTS[step];

  return (
    <Wrapper className={styles.wrapper}>
      <Row className={styles.hat}><Image height={40} className={styles.logoImage} src={logoImagePath}/></Row>
      <Container className={styles.main} style={{width: WIDTH}}>
        <Row className={styles.header}><span>Registration form</span></Row>

        <ProgressBar step={step}/>
        <CurrentStepComponent/>
        <Row className={styles.buttonContainer}>
          {step !== LAST_STEP && <Button
            className={styles.button}
            onClick={nextStep}
            title={step === LAST_STEP-1 ? 'register' : 'next step'}
          />}
        </Row>

        <SignFooter className={styles.footer}/>

      </Container>
    </Wrapper>
  );
};

const ProgressBar = ({step}) => (
  <Row className={styles.barContainer} style={{width: WIDTH+BAR_UNIT_WIDTH}}>
    <Row
      className={styles.barLine}
      style={{
        top: UNIT_ROUND/2,
        left: BAR_UNIT_WIDTH/2,
        width: WIDTH
      }}
    >{}</Row>
    {STEP_TEXTS.map((text, i) => <Unit text={text} complete={i < step} current={i === step}/>)}
  </Row>
);

const Unit = ({text, complete, current}) => (
  <div className={styles.barUnitContainer} style={{width: BAR_UNIT_WIDTH}}>
    <div
      className={cn(styles.barUnitRound, current && styles.current)}
      style={{width: UNIT_ROUND, height: UNIT_ROUND}}
    >
      {complete && <Check/>}
    </div>
    <div className={styles.barUnitText}>{text}</div>
  </div>
);

const Check = () => (
  <div
    className={styles.check}
    style={{
      width: UNIT_ROUND / 2,
      height: UNIT_ROUND / 3,
      marginTop: - UNIT_ROUND / 5,
    }}
  />
);

export default withOnLoadRender(SignUp);