import {Form} from "informed";
import styles from "./style.module.css";
import Input from "./Input";
import loginImagePath from "./images/login.png";
import passwordImagePath from "./images/password.png";
import Button from "../common/core/Button";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import Checkbox from "../common/core/Checkbox";
import {Link} from "react-router-dom";
import React from "react";
import InputPlaceholder from "./InputPlaceholder";
import fbImagePath from "./images/fb.png";
import glImagePath from "./images/gl.png";
import twImagePath from "./images/tw.png";
import Container from "react-bootstrap/Container";
import Image from "react-bootstrap/Image";

export default ({onSubmit}) => (
  <Form onSubmit={onSubmit} className={styles.form}>

    <Input
      field={"login"}
      placeholder={<InputPlaceholder imageSrc={loginImagePath} text={"Login"}/>}
    />
    <Input
      field={"password"}
      type={"password"}
      placeholder={<InputPlaceholder imageSrc={passwordImagePath} text={"Password"}/>}
    />

    <Row>
      <Col className={styles.rememberContainer}>
        <Checkbox field={"remember"}/>
        <span className={styles.rememberText}>Remember me</span>
      </Col>
      <Col className={styles.forgotText}>
        <Link to={""}>Forgot Login/Password</Link>
      </Col>
    </Row>

    <Row className={styles.buttonContainer}>
      <Button className={styles.button} title={'Login'} type={"submit"}/>
    </Row>

    <Row className={styles.signInWithContainer}><Col>or sign in with</Col></Row>
    <Row className={styles.servicesContainer}>
      <SignByLink href={""} imageSrc={fbImagePath}/>
      <SignByLink href={""} imageSrc={glImagePath}/>
      <SignByLink href={""} imageSrc={twImagePath}/>
    </Row>

  </Form>
);


const SignByLink = ({href, imageSrc}) => (
  <Col className="text-center">
    <Link to={href}><Image height={40} src={imageSrc}/></Link>
  </Col>
);