import {Form} from "informed";
import styles from "./style.module.css";
import Input from "./Input";
import loginImagePath from "./images/login.png";
import passwordImagePath from "./images/password.png";
import Button from "../common/core/Button";
import React from "react";
import InputPlaceholder from "./InputPlaceholder";
import Row from "react-bootstrap/Row";
import {Link} from "react-router-dom";

export default ({onSubmit}) => (
  <Form onSubmit={onSubmit} className={styles.form}>
    <Input
      field={"login"}
      placeholder={<InputPlaceholder imageSrc={loginImagePath} text={"Login"}/>}
    />

    <Input
      field={"password"}
      type={"password"}
      placeholder={<InputPlaceholder imageSrc={passwordImagePath} text={"Password"}/>}
    />

    <Input
      field={"email"}
      placeholder={<InputPlaceholder text={"Email"}/>}
    />

    <Input
      field={"company"}
      placeholder={<InputPlaceholder text={"Company name"}/>}
    />

    <Row className={styles.buttonContainer}>
      <Link to={'/register'}><Button className={styles.button} title={'Register'} type={"submit"}/></Link>
    </Row>

  </Form>
);