import React from 'react';
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import Input from "../Input";
import styles from '../style.module.css';
import {TextArea} from "informed";

export default () => (
  <Row className={"mt-4"}>

    <Row className={styles.subheader + ' mb-3'}>
      Your company description and contacts
    </Row>

    <Row>

      <Col className={"col-6 m-0 pr-2 text-center"}>

        <Input placeholder={'Company name'}/>
        <Input placeholder={'Date of establishment'}/>

        <Row>
          <Col className={"col-8 pr-2"}>
            <Input placeholder={'Street'}/>
            <Input placeholder={'City'}/>
            <Input placeholder={'Country'}/>
          </Col>
          <Col className={"col-4 pl-2"}>
            <Input placeholder={'№'}/>
            <Input placeholder={'Index'}/>
            <Input placeholder={'State'}/>
          </Col>
        </Row>

      </Col>

      <Col className={"col-6 m-0 pl-2 text-center"}>
        <Input placeholder={'Company registration number'}/>
        <Input placeholder={'Type of license, issued'}/>
        <Row>
          <Col className={"col-6 pr-2"}>
            <Input placeholder={'Certification'}/>
          </Col>
          <Col className={"col-6 pl-2"}>
            <Input placeholder={'Authorizations'}/>
          </Col>
        </Row>
        <Input placeholder={'Quality certificates, issued'}/>
        <Input placeholder={'Brand certificates and copyrights, issued'}/>
      </Col>

    </Row>

    <Row>
      <TextArea placeholder={'Text field max 3 propositions about founders and type of company'} className={styles.areaCompany}/>
    </Row>

  </Row>
);

