import React, {useState, useCallback} from "react";
import {Text} from "informed";
import styles from "./style.module.css";
import Row from "react-bootstrap/Row";

const HEIGHT = 35;

export default function Input({placeholder, ...otherProps}) {
  const [isPlaceholder, setPlaceholder] = useState(true);

  const onFocus = (e) => setPlaceholder(false);
  const onBlur = (e) => setPlaceholder(!e.target.value);

  return (
    <Row className={styles.inputContainer}>
      <div className={styles.container} style={{height: HEIGHT, borderRadius: HEIGHT/2}}>
        <div className={styles.placeholder} style={{height: HEIGHT}}>
          {isPlaceholder && placeholder}
        </div>
        <Text
          onFocus={onFocus}
          onBlur={onBlur}
          className={styles.input}
          autoComplete={"off"} style={{height: HEIGHT}}
          {...otherProps}
        />
      </div>
    </Row>
  );
};